<template>
  <div>
    <el-button class="back" @click="$router.back()">返回列表</el-button>
    <div class="shopcar" v-if="shop.length>0">
      <table class="biao" border="1" style="border-collapse:collapse">
        <tr>
          <th>选择</th>
          <th>商品照片</th>
          <th>商品名称</th>
          <th>商品单价</th>
          <th>商品数量</th>
          <th>操作</th>
        </tr>
        <tr v-for="(item,index) in shop" :key="item.Id">
          <td>
            <input class="in1" type="checkbox" v-model="checks" :value="item" />
          </td>
          <td>
            <img :src="item.img_url" alt />
          </td>
          <td>{{item.title}}</td>
          <td>{{item.price}}</td>
          <td>
            <button @click="rede(item.Id,index)">-</button>
            <button>{{item.count}}</button>
            <button @click="inc(item.Id,index)">+</button>
          </td>
          <td>
            <button class="bt" @click="del(item.Id,index)">删除</button>
          </td>
        </tr>
      </table>
      <p class="jiage">
        <input type="checkbox" class="quan in1" v-model="flag" @change="all" />
        <span>全选</span>

        <button class="btn" @click="delAll()">删除所选</button>
        <span class="zong">总计:￥{{total}}元</span>
        <button class="btn" @click="money">立即结算</button>
      </p>
    </div>
    <div v-else class="kong">
      <span>您的购物车空空如也</span>
      <a class="xian" href="/list/咖啡">快去添加</a>
    </div>
  </div>
</template>
<script>
import $ajax from "../plugins/ajax";
export default {
  data() {
    return {
      total: 0,
      shop: [],
      checks: [],
      flag: false
    };
  },
  methods: {
    getList: async function() {
      var res = await this.$ajax(
        "http://vebcoder.cn:9527/api/shoplist?token=" + this.$store.state.token
      );
      this.shop = res.data;
    },
    del: async function(a, index) {
      var res = await this.$ajax(
        "http://vebcoder.cn:9527/api/del?goodId=" +
          a +
          "&token=" +
          this.$store.state.token
      );
      if (res.data.code) {
        if (this.checks[index]) {
          var price = Number(this.shop[index].price);
          this.total -= price * this.shop[index].count;
        }
        this.$message({
          message: "删除成功",
          type: "success"
        });
        this.checks.splice(index, 1);
        this.shop.splice(index, 1);
      }
    },
    // 增加
    inc: async function(a, index) {
      var token = this.$store.state.token;
      var res = await this.$ajax("/add?token=" + token + "&goodId=" + a);
      if (res.data.code) {
        this.shop[index].count++;
      }
    },
    // 减少
    rede: async function(a, index) {
      if (this.shop[index].count > 1) {
        var token = this.$store.state.token;
        var res = await this.$ajax("/remove?token=" + token + "&goodId=" + a);
        if (res.data.code) {
          this.shop[index].count--;
        }
      } else {
        this.$message.error("不能再减啦");
      }
    },
    delAll: async function() {
      var x = [];
      for (let i = 0; i < this.checks.length; i++) {
        if (this.checks[i]) {
          x.push(
            new Promise(
              async function(resolve) {
                var res = await $ajax(
                  "/del?token=" +
                    decodeURIComponent(this.$store.state.token) +
                    "&goodId=" +
                    this.shop[i].Id
                );
                resolve(res.data.code);
              }.bind(this)
            )
          );
        }
      }
      var delflag = true;
      Promise.all(x).then(res2 => {
        res2.forEach(item => {
          if (item == 2 || item == 0) {
            this.$message({
              message: "删除失败",
              type: "warning"
            });
            delflag = false;
          }
          if (delflag) {
            this.$message({
              message: "删除成功",
              type: "success"
            });
          }
          for (var i = 0; i < this.checks.length; i++) {
            if (this.checks[i]) {
              this.shop.splice(i, 1);
              this.checks.splice(i, 1);
              i--;
            }
          }
          if (this.checks.length == 0) {
            this.flag = false;
          }
        });
      });
    },
    all: function() {
      if (this.flag == true) {
        this.checks = this.shop;
      } else {
        this.checks = [];
      }
    },
    money() {
      if(this.total==0){
          this.$message({
        message: "请选择您要购买的商品",
        type: "warning"
      });
      }else{
          this.$message({
        message: "支付成功",
        type: "success"
      });
      }
    }
  },
  created() {
    this.getList();
  },
  watch: {
    // 算总价
    checks: {
      handler: function(val) {
        this.total = 0;
        for (var i = 0; i < val.length; i++) {
          var price = Number(val[i].price.replace("￥", ""));
          this.total += price * val[i].count;
        }
        this.flag = val.length == this.shop.length;
        if (val.length == 0) {
          this.flag = false;
        }
      },
      deep: true
    },
    $route() {
      this.getList();
    }
  }
};
</script>
<style scoped>
.shopcar {
  width: 1000px;
  height: 360px;

  overflow: auto;
  margin: 0 auto;
}
.kong {
  width: 230px;

  margin: 200px auto;
}
.biao .bt {
  width: 50px;
  height: 26px;
  border: none;
  border-radius: 2px;
  outline: none;
  background: coral;
  color: white;
}
.kong .xian {
  color: blue;
  margin-left: 5px;
}
.in1 {
  width: 16px;
  height: 16px;
  vertical-align: middle;
}

.biao {
  width: 1000px;
  height: 40px;
}
.biao td {
  height: 50px;
}

.quan {
  margin-left: 26px;
  margin-right: 5px;
}
.biao th {
  line-height: 40px;
}

.biao img {
  height: 40px;
  display: block;
  margin: 0 auto;
}

.jiage .btn {
  width: 70px;
  height: 26px;
  border: none;
  background: coral;
  color: white;
  border-radius: 2px;
  outline: none;
  margin-left: 20px;
}

.jiage {
  line-height: 40px;
  background: #dedede;
  position: relative;
}
.zong {
  margin-left: 20px;
}

.biao td {
  text-align: center;
}

.biao button {
  width: 30px;
}
</style>